#contact{
    width: 100vw;
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
}
.contactTitle{
    font-size: 3rem;
    margin-bottom: 1rem;
}
.contactDesc{
    padding: 1rem;
    font-size: medium;
    font-weight: 300;
}

.contactForm{
    margin: 1rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 90vw;
    max-width: 60rem;
}
.name, .email, .msg{
    font-size: medium;
    width: 100%;
    max-width: 40rem;
    margin: 0.5rem;
    padding: 0.5rem 1rem;
    color: white;
    border: none;
    border-radius: 0.5rem;
    background: hsl(0, 0%, 16%);
}
.submitBtn{
    background: hsl(0, 0%, 100%);
    border: none;
    border-radius: .5rem;
    margin: 2rem;
    padding: .75rem 3.5rem;
    cursor: pointer;
}
.submitBtn:hover{
    background: hsl(0, 0%, 80%);
}
.links{
    display: flex;
    flex-wrap: wrap;
}

.link {
    object-fit: cover;
    height: 3rem;
    width: 3rem;
    margin: 0 0.75rem;
}